import $ from "jquery";
import Swiper, { Pagination, Navigation } from 'swiper';
import AOS from 'aos';

import 'normalize.css';
import 'swiper/swiper-bundle.css';
import 'aos/dist/aos.css'; 
import '../scss/global.scss';
import '../scss/help.scss';

var data = [
  "<h2 class='subheading aos-init aos-animate' data-aos='zoom-in-up'>Account Verification</h2><p>Verifying your SLIP account is quick and simple and only requires submission of a verified government Identification Card.</p><p>Users may verify their accounts through the account tab on the SLIP app, under account verification.</p>",
  "<h2 class='subheading aos-init aos-animate' data-aos='zoom-in-up'>Account Access</h2><p>Each SLIP user account is  is linked to your mobile phone number upon sign up, which is verified by a One-Time Password (OTP).</p><p>Upon sign-up and registration, user also sets up a 6 digit PIN which is used to authorize actions and transactions on SLIP.</p>",
  "<h2 class='subheading aos-init aos-animate' data-aos='zoom-in-up'>Payments</h2><p>All payments on the SLIP app are secured and verified by our third-party payment gateway partners to ensure smooth and safe transactions between users.</p>",
  "<h2 class='subheading aos-init aos-animate' data-aos='zoom-in-up'>Cash Outs</h2><p>Ledger cashouts of each user are secured by our banking institution partners through their Interbank Fund Transfer facility (IBFT) via Instapay.</p>",
  "<h2 class='subheading aos-init aos-animate' data-aos='zoom-in-up'>Community Selling</h2><p>SLIP stands for Social-Lifestyle-Interactive-Payments.</p><p>Our goal is to empower every online community and community seller by providing them the functionality to create and manage their own online communities.</p><p>The possibilities are endless and are only limited by each user’s imagination. We encourage constant interaction between users and provide peer-to-peer payments through our mobile application.</p>",
  "<h2 class='subheading aos-init aos-animate' data-aos='zoom-in-up'>Account Recovery</h2><p>Easy recovery of SLIP PIN through the user’s linked cellphone number. A dedicated support email address is also available for any concerns and issues that might arise.</p><p>Community managers are also available to community leaders for any quick queries and concerns.</p>",
];

Swiper.use([Navigation, Pagination]);

$(document).ready(function () {
    AOS.init();

    var swiper = new Swiper('.swiper-container', {
        slidesPerView: 1,
        spaceBetween: 30,
        centeredSlides: true,
        roundLengths: true,
        loop: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
        },
        breakpoints: {
          640: {
            slidesPerView: 2,
            spaceBetween: 20,
          },
          1024: {
            slidesPerView: 3,
            spaceBetween: 50,
          },
        }
      });


      $('.nav__btn').on('click', function() {
        $('.slip__navbar').toggleClass('collapsed');
      });


      $('.accordion__header').on('click', function() {
        // var targetElem = $(this).next('.accordion__body'),
          var  icon = $(this).children('.accordion__icon'),
            arrowDown = '<svg width="15" height="10" xmlns="http://www.w3.org/2000/svg"><path d="M2.15.5L7.5 6.065 12.85.5l1.65 1.717-7 7.283-7-7.283z" fill="#2178FF" fill-rule="nonzero"/></svg>',
            arrowRight = '<svg width="9" height="14" xmlns="http://www.w3.org/2000/svg"><path d="M0 12.35L5.565 7 0 1.65 1.717 0 9 7l-7.283 7z" fill="#2178FF" fill-rule="nonzero"/></svg>';

        $('.accordion__item.active .accordion__icon').html(arrowDown);
        $('.accordion__item.active').removeClass('active');

        // targetElem.parent().addClass('active');
        $(this).parent().addClass('active');
        if ($(this).parent().hasClass('active')) {
          icon.html(arrowRight);
        } else {
          icon.html(arrowDown);
        }


        var index = $(this).attr('data-i');
        $('.viewer').html(data[index]);


      });


      $('.accordion__item.active .accordion__header').trigger('click');

});
